.vinyl { position: relative; display: inline-block; width: 288px; height: 288px; border-radius: 50%; background-color: #040504; box-shadow: 1px 1px 10px #000; background-image: url(../img/case1/bitmap.png); background-size: cover; background-position: center; background-repeat: no-repeat; }

.vinyl .play { background-color: transparent; width: 100%; z-index: 10; content: ""; position: absolute; top: 0; left: 0; right: 0; bottom: 0; }

.macaron { position: absolute; top: 50%; left: 50%; z-index: 5; margin: -16% 0 0 -16%; width: 33.33%; height: 33.33%; border-radius: 50%; }

.macaron .cover { display: flex; align-items: center; justify-content: center; font-size: 40px; width: 100%; height: 100%; border-radius: 50%; background-image: linear-gradient(226deg, #92e41a, #4cb9ee); }

.grooves { position: absolute; top: 2%; right: 2%; bottom: 2%; left: 2%; z-index: 5; border-radius: 50%; background-image: radial-gradient(center center, circle closest-side, transparent 43%, transparent 53%, #000 54%, transparent 54%, transparent 64%, #000 65%, transparent 65%, transparent 75%, #000 76%, transparent 76%, transparent 86%, #000 87%, transparent 87%, transparent 97%, #000 98%, transparent 98%); }

.play .macaron, .play .grooves, .play .macaron .cover { animation: rotation 4s infinite linear; }

.paused .macaron, .paused .grooves, .paused .macaron .cover { animation-play-state: paused; }

.vinyl-picture-disc .macaron { top: 0%; left: 0%; z-index: 4; margin: 0; width: 100%; height: 100%; }

.vinyl-picture-disc .macaron:after { margin: -1.25% 0 0 -1.25%; width: 2.5%; height: 2.5%; background: white; }

.song { display: block; margin: 50px 50px 0; width: 400px; text-align: center; }

.song audio { display: none; }

@keyframes rotation { 0% { transform: rotate(0); }
  100% { transform: rotate(360deg); } }

@keyframes reflection { 0% { filter: blur(5px);
    transform: rotate(0); }
  25% { transform: rotate(5deg); }
  50% { filter: blur(10px);
    transform: rotate(0); }
  75% { transform: rotate(-5deg); }
  100% { filter: blur(5px);
    transform: rotate(0); } }

