body {
	font-family: $Futuras;
	color: rgba($black, 0.5);
	@include f(20px,500,1.5);
	min-width: 1260px;
	@include font-smoothing;
	letter-spacing: 0;
	background-color: $white;
	&.is-dark {
		color: rgba($white, .5);
		background-color: $darkBg;
		.title {
			color: $white; } }
	&.no-min-width {
		min-width: inherit; } }

.title {
	font-family: $Futuras;
	@include f(30px,500,1.333);
	color: $black;
	@include css-lock(21,30,1024,1440);
	&_bold {
		font-weight: 900;
		font-size: 30px;
		@include md {
			font-size: 30px; } } }
.title-bg {
	font-family: $Futuras;
	font-weight: 500;
	line-height: 1;
	font-size: 280px;
	color: rgba($black-mine,.03);
	position: absolute;
	text-transform: uppercase;
	text-align: center;
	z-index: -1;
	right: 50%;
	transform: translateX(50%);
	top: 0;
	@include r(1440) {
		font-size: calc(64px + (280 - 64)*((100vw - 320px)/(1440 - 320))); }
	@include sm {
		font-weight: 700; }
	&_big {
		@include r(1440) {
			font-size: calc(72px + (280 - 64)*((100vw - 320px)/(1440 - 320))); } }
	&_xlg {
		font-size: 340px;
		@include r(1440) {
			font-size: calc(72px + (340 - 72)*((100vw - 320px)/(1440 - 320))); } } }

.title-roboto {
	font-family: $Roboto;
	font-weight: 700;
	font-size: 48px;
	line-height: 1.2;
	text-transform: uppercase;
	color: #d0b560;
	@include sm {
		font-size: 36px; } }


.light {
	font-weight: 300; }

.prev-head {
	margin-top: 60px; }

.text-upper {
	text-transform: uppercase; }
